.friendList {
    /* text-align: center; */
    display: grid;
    justify-content: center;
}

.item {
    display: flex;
    align-items: center;
    margin: 15px;
    width: 300px;
    height: 125px;
    box-sizing: border-box;
    background-color: white;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    
  }
  .item span {
    display: block;
    width: 10px;
    height: 10px;
    margin-left: 20px;
    border-radius: 50%;
  }
  .statusOnline {
    background-color: green;
  }
  .statusOffline {
    background-color: tomato;
  } 
  .avatar {
    margin-left: 10px;
    margin-right: 10px;
  }
  
  .name {
    font-weight: bold;
    font-size: 18px;
    line-height: 45px;
  }

